<template><section><h1>alert</h1>
<template>
    <coding :code="def" title="简单的" content="有4种样式" lang="html">
        <y-alert content="一个小小的alert"></y-alert>
        <y-alert content="一个小小的alert" type="success"></y-alert>
        <y-alert content="一个小小的alert" type="warning"></y-alert>
        <y-alert content="一个小小的alert" type="danger"></y-alert>
    </coding>
    <coding :code="def" title="内容" content="内容多的可以这样使用" lang="html">
        <y-alert title="一个小小的alert" type="danger" content="一个小小的alert"></y-alert>
        <y-alert title="一个小小的alert" type="danger" content="一个小小的alert" close-text="删除"></y-alert>
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>样式</td>
<td><code>String</code></td>
<td><code>info</code></td>
<td><code>success</code>,<code>warning</code>,<code>danger</code></td>
</tr>
<tr>
<td>title</td>
<td>标题</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>close</td>
<td>是否显示关闭</td>
<td><code>Boolean</code></td>
<td><code>true</code></td>
<td>-</td>
</tr>
<tr>
<td>CloseText</td>
<td>关闭键内容(关闭按钮将隐藏)</td>
<td><code>String</code></td>
<td>-</td>
<td>-</td>
</tr>
</tbody>
</table>
</section></template>

<script>
let def =
`<template>
    <y-alert content="一个小小的alert"></y-alert>
    <y-alert content="一个小小的alert" type="success"></y-alert>
    <y-alert content="一个小小的alert" type="warning"></y-alert>
    <y-alert content="一个小小的alert" type="danger"></y-alert>
</template>`
let cont =
`<template>
    <y-alert content="一个小小的alert" type="danger" content="一个小小的alert"></y-alert>
    <y-alert content="一个小小的alert" type="danger" content="一个小小的alert" close-text="删除"></y-alert>
</template`
export default {
    data(){
        return {
            def:def
        }
    }
}
</script>


